.upload {
  background-color: #f5fffa;
  padding-bottom: 50px;
}

.head-u {
  border-bottom: 1px solid #000;
}

.top-u {
  padding-top: 10px;
  margin-right: 15%;
  margin-left: 20%;
  align-items: center;
  justify-content: center;
  position: relative;
}

.not-icon {
  position: relative;
  float: right;
  font-size: 20px;
  cursor: pointer;
}

.position-absolute-u {
  height: 10px;
  width: 10px;
}

.form-control:focus,
.submit-button:focus,
.form-label:focus {
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
  background-color: inherit;
}

.form-check-input:focus {
  outline: none !important;
  border: 0.5px solid gray !important;
  box-shadow: none !important;
}

.upload-u {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}

.row {
  width: 100%;
  padding: 0 20px;
  display: flex !important;
}

.col-md-4 {
  display: flex !important;
  flex-direction: column !important;
  width: 80%;
}

.col-md-4 > textarea {
  border: none;
  border-bottom: 1px solid gray;
  height: 50px;
  background-color: transparent;
  margin-top: -10px;
}

.col-md-4 > select {
  border: none;
  border-bottom: 1px solid gray;
  height: 50px;
  background-color: transparent;
  margin-top: -10px;
}

.col-md-4 > label {
  height: 50px;
  margin-bottom: 20px;
}

.submit-button {
  padding: 5px 25px;
  border: none;
  border-radius: 20px;
  background-color: #2e8b57;
  color: #ffff;
  font-size: 1rem;
  width: 60%;
  margin-top: 40px;
}

.submit-button:disabled {
  cursor: not-allowed;
  background-color: #2e8b56c5;
}

.submit-button:hover {
  background-color: #306754;
  transition: 1s ease-in;
}
